// Manage Billing screen
import { Grid, Paper, Typography } from "@mui/material";
import { blue, red } from "@mui/material/colors";
import { Box } from "@mui/system";
import * as React from "react";
import BillingFooter from "../components/Billing/footer";
import Stage from "../components/Billing/stage";
import BillList from "../components/Billing/table";
import ProtectedRoute from "../utils/protectedRoute";
import { useLocation, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import QueryString from "qs";
import api from "../utils/api_handler";
import evalJsonata from "../utils/evanJsonata";
import { loadCart, reset } from "../app/feature/cartSlice";

function Page() {
  let { id } = useParams();
  let cart = useSelector((state) => state.cart);
  let dispatch = useDispatch();

  let [loaded, setLoaded] = React.useState({
    status: false,
    msg: "Fetching Bill",
  });

  const fetchPurchase = async () => {
    let query = QueryString.stringify({
      populate: {
        bill_items: {
          populate: ["item"],
        },
      },
    });

    let { status, data, msg } = await api(
      `/bills/${id}?${query}`,
      "get",
      null,
      true
    );
    if (status) {
      console.log("bill", data);

      let items = evalJsonata(
        data,
        `data.**.bill_items.data.$.{
          "id":**.item.**.id,
          "name":**.item.**.name,
          "costPrice":**.costPrice,
           "margin":**.margin,
           "qty":**.qty,
           "price":**.price,
           "rate":**.item.**.rate,
           "taxedCostPrice":**.taxedCostPrice,
           "tax":**.item.**.tax
      }`
      );

      items = items && !Array.isArray(items) ? [items] : items ?? [];

      console.log("items", items);
      dispatch(
        loadCart({
          id: id,
          bill: evalJsonata(
            data,
            `data.$.{
            "id":id,
            "invoiceNo":attributes.id,
            "expense":attributes.expense ? attributes.expense : "",
            "date":attributes.createdAt ? attributes.createdAt : "",
            "subTotal":attributes.subTotal? attributes.subTotal : "",
            "tax":attributes.tax? attributes.tax : "",
            "method":attributes.method? attributes.method : "",
            "discount":attributes.discount? attributes.discount : "",
            "total":attributes.total? attributes.total : "",
           
            "items":$count(**.items.**.id),
            "supplier":**.supplier.**.name
            }`
          ),
          cart: items ?? [],
          locked: true,
        })
      );
      setLoaded({
        status: true,
        msg: msg,
      });
    } else {
      setLoaded({
        status: false,
        msg: msg,
      });
    }
  };

  React.useEffect(
    (_) => {
      dispatch(reset());
      console.log("Location", id);
      if (id && id != "new") {
        console.log("here");
        fetchPurchase();
      } else {
        setLoaded({
          status: true,
        });
      }
    },
    [id]
  );

  if (!loaded.status) {
    return (
      <Paper
        elevation={7}
        sx={{
          margin: "20px auto",
          width: 300,
          padding: 4,
          background: "Orange",
          color: "white",
        }}
      >
        <Typography variant="h6">{loaded.msg}</Typography>
      </Paper>
    );
  }

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",

        height: "calc(100vh - 48px)",
        padding: "12px 16px",
      }}
    >
      <Box sx={{ height: "80px" }}>
        <Stage />
      </Box>

      <Box sx={{ height: "calc(100vh - 290px)" }}>
        <BillList maxHeight={"calc(100vh - 290px)"} />
      </Box>

      <Box sx={{ height: "100px" }}>
        <BillingFooter />
      </Box>
    </Box>
  );
}

export default function Screen() {
  return (
    <ProtectedRoute>
      <Page />
    </ProtectedRoute>
  );
}
