import { RefreshTwoTone, ViewColumn } from "@mui/icons-material";
import { Button, IconButton } from "@mui/material";
import { Box } from "@mui/system";
import { useDispatch, useSelector } from "react-redux";
import { reset, setBillNo, setPrinted } from "../../app/feature/returnsSlice";
import api from "../../utils/api_handler";

export default function BillingOps() {
  let returns = useSelector((state) => state.returns);
  let dispatch = useDispatch();
  async function createBill() {
    // create a bill
    console.log("returnsData", returns);
    if (!returns.id) {
      let { subTotal, taxTotal, method, total, discount, value, billNo } =
        returns;
      let billData = {
        data: {
          bill: billNo,
          subTotal: subTotal,
          taxTotal: taxTotal,
          method: method,
          total: total,
          discount: discount,
        },
      };

      let { status, data, msg } = await api("/returns", "post", billData, true);

      console.log("Bill", status, data);
      if (status) {
        dispatch(setBillNo(data.data.id));
        let billItems = Object.values(value).map((_) => {
          return {
            data: {
              item: _.id,
              return: data.data.id,
              qty: _.qty,
              rate: _.rate,
              tax: _.tax,
            },
          };
        });

        for (let index = 0; index < billItems.length; index++) {
          const billItem = billItems[index];
          let { status, data, msg } = await api(
            "/return-items",
            "post",
            billItem,
            true
          );
          console.log("BillItems", status);
        }
      }
    }
  }

  function resetHandle() {
    if (!returns.id) {
      if (window.confirm("Do You want to reset ?")) {
        dispatch(reset());
      }
    } else {
      if (returns.printed) {
        dispatch(reset());
      } else {
        alert("Please Print First");
      }
    }
  }

  return (
    <Box
      sx={{
        display: "flex",
        direction: "column",
        justifyContent: "space-around",
      }}
    >
      {!returns.locked && (
        <Button
          onClick={createBill}
          disabled={!returns.id && +returns.total > 0 ? false : true}
          variant="contained"
          color="secondary"
        >
          Return
        </Button>
      )}

      <Button
        variant="contained"
        disabled={returns.printed || !returns.id}
        onClick={() => dispatch(setPrinted())}
        color="primary"
      >
        Print
      </Button>

      {!returns.locked && (
        <Button
          variant="contained"
          disabled={!(+returns.total > 0)}
          onClick={resetHandle}
          color="primary"
        >
          Reset
        </Button>
      )}
    </Box>
  );
}
