import { RefreshTwoTone, ViewColumn } from "@mui/icons-material";
import { Button, IconButton } from "@mui/material";
import { Box } from "@mui/system";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { loadCart } from "../../app/feature/barcodeSlice";
import {
  reset,
  setBillNo,
  setLocked,
  setPrinted,
} from "../../app/feature/inventorySlice";
import api from "../../utils/api_handler";
import evalJsonata from "../../utils/evanJsonata";

export default function PurchaseOps() {
  let cart = useSelector((state) => state.inventory);
  let dispatch = useDispatch();
  let nav = useNavigate();
  let alert = useAlert();
  async function createBill() {
    // create a bill
    console.log("cartData", cart);
    if (cart.id) {
      let { billTotal: total, purchaseOrder, value } = cart;
      // let billData = {
      //   data: {
      //     subTotal: subTotal,
      //     taxTotal: taxTotal,
      //     method: method,
      //     total: total,
      //     discount: discount,
      //   },
      // };

      // let { status, data, msg } = await api("/bills", "post", billData, true);

      // console.log("Bill", status, data);

      // dispatch(setBillNo(data.data.id));
      let billItems = Object.values(value).map((_) => {
        return {
          data: {
            product: _.id,
            purchase_order: purchaseOrder.id,
            purchaseQty: _.purchaseQty,
            costPrice: _.costPrice,
            tax: _.tax,
            taxedCostPrice: _.taxedCostPrice,
            expense: _.expense,
            deduction: _.deduction,
            margin: _.margin,
            rate: _.rate,
            price: _.price,
            stock: _.purchaseQty,
          },
        };
      });

      console.log("billed", billItems);
      let success = [];

      for (let index = 0; index < billItems.length; index++) {
        const billItem = billItems[index];

        let { status, data, msg } = await api("/items", "post", billItem, true);

        if (status) {
          success.push(data);
        }
      }

      console.log("success", success);

      if (billItems.length == success.length) {
        alert.success("Saved Success Fully !");

        dispatch(setLocked(true));
      } else {
        if (success.length <= 0) {
          alert.error("Error Saving all The Items");
        } else {
          alert.error(
            "Some Items were saved, Please Donot Use the Items in This list"
          );
          let delList = evalJsonata(success, `**.id`);
          delList = Array.isArray(delList) ? delList : [delList];
          for (let index = 0; index < delList.length; index++) {
            let { status, data, msg } = await api(
              `/items/${delList[index]}`,
              "delete",
              null,
              true
            );
          }
        }
      }
    }
  }

  function resetHandle() {
    if (!cart.id) {
      if (window.confirm("Do You want to reset ?")) {
        dispatch(reset());
      }
    } else {
      if (cart.printed) {
        dispatch(reset());
      } else {
        alert("Please Print First");
      }
    }
  }

  function handlePrint() {
    dispatch(
      loadCart({
        value: Object.values(cart.value),
      })
    );

    nav("/barcode");
  }

  return (
    <>
      <Box
        sx={{
          display: "flex",
          direction: "column",
          justifyContent: "space-around",
        }}
      >
        <Button
          onClick={createBill}
          disabled={
            cart.id &&
            Number(cart.billTotal) > 0 &&
            Number(cart.billTotal) ==
              (cart.purchaseOrder ? Number(cart.purchaseOrder.total) : 0) &&
            !cart.locked
              ? false
              : true
          }
          variant="contained"
          color="secondary"
        >
          Save
        </Button>

        <Button
          variant="contained"
          disabled={cart.printed || !cart.id}
          onClick={handlePrint}
          color="primary"
        >
          Print
        </Button>
        <Button
          variant="contained"
          disabled={!(+cart.total > 0)}
          onClick={resetHandle}
          color="primary"
        >
          Reset
        </Button>
      </Box>
    </>
  );
}
