import axios from "axios";

let url = "http://localhost:1337/api";
export default async function api(path, method, data, auth = false) {
  console.log("Api Called");
  var config = {
    method: method,
    url: `${url}${path}`,
    headers: {
      "Content-Type": "application/json",
    },
    data: data,
    validateStatus: () => true,
  };

  if (auth) {
    config.headers["Authorization"] = `Bearer ${
      JSON.parse(window.atob(localStorage.getItem("_userdata"))).jwt
    }`;
  }

  let res = await axios(config);

  console.log(res);
  if (res.status == 200) {
    return {
      status: true,
      data: res.data,
      msg: "Success",
    };
  } else {
    return {
      status: false,
      data: "",
      err: res.data.error,
      msg: res.data.error.message,
    };
  }
}
