import Barcode from "react-barcode";
import { useSelector } from "react-redux";

export default function BillPrintTemplate() {
  let cart = useSelector((state) => state.barcode);

  let opt = {
    width: 0.8,
    height: 80,
    format: "CODE128",
    displayValue: true,
    fontOptions: "",
    font: "monospace",
    textAlign: "center",
    textPosition: "bottom",
    textMargin: 2,
    fontSize: 6,
    background: "#ffffff",
    lineColor: "#000000",
    margin: 2,
  };

  return (
    <div id="billPrint" className="bill-print">
      <div
        id="orders_msg"
        className="ticket"
        style={{ display: "flex", flexWrap: "wrap", width: "100%" }}
      >
        {cart.value &&
          Object.values(cart.value).map((_) => {
            // return Array.apply(null, Array(+_.qty)).forEach((element) => {
            let arr = [];

            for (let index = 0; index < +_.qty; index++) {
              arr.push(
                <div
                  style={{
                    width: "50%",
                  }}
                >
                  <div
                    style={{
                      padding: "2mm",
                      display: "flex",
                      justifyContent: "center",
                      flexDirection: "column",
                      alignItems: "center",
                    }}
                  >
                    <p>{_.name}</p>
                    <Barcode value={_.barcode} {...opt} />
                    <p>{_.price}</p>
                  </div>
                </div>
              );
            }
            return arr;
            // });
          })}
      </div>
    </div>
  );
}
