// Manage Billing screen
import { Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";
import QueryString from "qs";
import * as React from "react";
import { useDispatch } from "react-redux";
import { useLocation, useParams } from "react-router-dom";
import { loadCart } from "../app/feature/inventorySlice";
import BillingFooter from "../components/Purchase-Inventory/footer";
import PurchaseOps from "../components/Purchase-Inventory/ops";
import Stage from "../components/Purchase-Inventory/stage";
import BillList from "../components/Purchase-Inventory/table";
import api from "../utils/api_handler";
import evalJsonata from "../utils/evanJsonata";
import ProtectedRoute from "../utils/protectedRoute";

function Page() {
  let dispatch = useDispatch();
  let { id } = useParams();
  let [loaded, setLoaded] = React.useState({
    status: false,
    msg: "Fetching Purchase Order",
  });

  const fetchPurchase = async () => {
    let query = QueryString.stringify({
      populate: {
        items: {
          populate: ["product"],
        },
      },
    });

    let { status, data, msg } = await api(
      `/purchase-orders/${id}?${query}`,
      "get",
      null,
      true
    );
    if (status) {
      console.log("purchase-order", data);

      let items = evalJsonata(
        data,
        `data.**.items.data.$.{
          "id":**.product.**.id,
          "name":**.product.**.name,
          "barcode":**.barcode,
          "costPrice":**.costPrice,
           "margin":**.margin,
           "purchaseQty":**.purchaseQty,
           "taxedCostPrice":**.taxedCostPrice,
           "billedAmt":(**.taxedCostPrice * **.purchaseQty),
           "tax":**.tax
      }`
      );

      items = items && !Array.isArray(items) ? [items] : items;
      dispatch(
        loadCart({
          id: id,
          purchaseOrder: evalJsonata(
            data,
            `data.$.{
            "id":id,
            "invoiceNo":attributes.invoiceNo,
            "expense":attributes.expense ? attributes.expense : "",
            "date":attributes.date ? attributes.date : "",
            "subTotal":attributes.subTotal? attributes.subTotal : "",
            "tax":attributes.tax? attributes.tax : "",
            "discount":attributes.discount? attributes.discount : "",
            "payable":attributes.payable? attributes.payable : "",
            "total":attributes.total? attributes.total : "",
            "items":$count(**.items.**.id),
            "supplier":**.supplier.**.name
            }`
          ),
          cart: items ?? [],
          locked: items && items.length > 0 ? true : false,
        })
      );
      setLoaded({
        status: true,
        msg: msg,
      });
    } else {
      setLoaded({
        status: false,
        msg: msg,
      });
    }
  };

  React.useEffect((_) => {
    console.log("Location", id);
    fetchPurchase();
  }, []);

  if (!loaded.status) {
    return (
      <Paper
        elevation={7}
        sx={{
          margin: "20px auto",
          width: 300,
          padding: 4,
          background: "Orange",
          color: "white",
        }}
      >
        <Typography variant="h6">{loaded.msg}</Typography>
      </Paper>
    );
  }

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",

        height: "calc(100vh - 48px)",
        padding: "12px 16px",
      }}
    >
      <Box sx={{ height: "80px" }}>
        <Stage />
      </Box>

      <Box sx={{ height: "calc(100vh - 290px)" }}>
        <BillList maxHeight={"calc(100vh - 290px)"} />
      </Box>
      <Box sx={{ height: "100px" }}>
        <BillingFooter />
      </Box>
    </Box>
  );
}

export default function InvScreen() {
  return (
    <ProtectedRoute>
      <Page />
    </ProtectedRoute>
  );
}
