import {
  Autocomplete,
  Grid,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import QueryString from "qs";

import React, { useEffect } from "react";
import api from "../../utils/api_handler";
import AntSwitch from "../shared/AntSwitch";
import { useDebouncedValue } from "@mantine/hooks";
import { useDispatch, useSelector } from "react-redux";
import { addToCart, removeFromStage } from "../../app/feature/returnsSlice";
import BillingOps from "./ops";
import { useAlert } from "react-alert";

let formInit = {
  id: "",
  barcode: "",
  name: "",
  searchName: "",
  qty: "",
  price: "",
  tax: "",
};
export default function Stage() {
  let qtyRef = React.useRef();
  let barRef = React.useRef();

  let stage = useSelector((state) => state.returns.stage);
  let items = useSelector((state) => state.returns.items);

  const [checked, setChecked] = React.useState(true);
  const [focusState, setFocusState] = React.useState("");
  const [timer, setTimer] = React.useState(null);
  const [options, setOptions] = React.useState([]);
  const [search, setSearch] = React.useState("");
  const handleChangeMode = (event) => {
    setChecked(event.target.checked);
  };

  let [form, setForm] = React.useState(formInit);

  let dispatch = useDispatch();
  let alert = useAlert();

  async function handleKeyPress(event) {
    // barcode handle
    if (event.target.name == "barcode") {
      if (event.key === "Enter") {
        let query = QueryString.stringify({
          filters: {
            barcode: {
              $containsi: form.barcode,
            },
          },
        });
        let res = await api(`/items?${query}`, "get", null, true);
        if (res.status) {
          let items = res.data.data.map((e) => ({ id: e.id, ...e.attributes }));
          setForm((old) => ({ ...old, ...items[0] }));

          setFocusState("qty");
          qtyRef.current.focus();
        }
      }
    }

    if (event.target.name == "qty") {
      if (event.key === "Enter") {
        dispatch(removeFromStage());

        if (items.length > 0) {
          let it = items.filter((e) => e.id == form.id);

          if (it.length <= 0) {
            alert.error(`${form.name} not in Bill`);
            return;
          } else {
            if (+it[0].qty != +form.qty) {
              alert.error(`${it[0].qty} qty was billed, check Quantity`);
              return;
            }
          }
        }
        dispatch(addToCart(form));

        setForm(formInit);

        setFocusState("bar");
        barRef.current.focus();
      }
    }
  }

  function handleChange({ target }) {
    setForm((old) => ({ ...old, [target.name]: target.value }));
  }

  function handleSearch(event, newValue) {
    setSearch(newValue);
    clearTimeout(timer);
    let newTimer = setTimeout(async (_) => {
      let query = QueryString.stringify({
        filters: {
          searchName: {
            $containsi: newValue,
          },
        },
      });
      let res = await api(`/items?${query}`, "get", null, true);
      let items = res.data.data.map((e) => ({ id: e.id, ...e.attributes }));
      setOptions(items);
      console.log(res);
    }, 500);

    setTimer(newTimer);
  }

  useEffect(
    (_) => {
      console.log("staged", stage);
      if (stage) {
        setForm(stage);
        setSearch(stage.name);
        // dispatch(removeFromStage());
      }
    },
    [stage]
  );
  return (
    <Grid
      container
      direction={"row"}
      spacing={2}
      justifyContent="space-between"
      alignItems="center"
    >
      <Grid item xs={7}>
        <Grid container direction={"row"} spacing={2} alignItems="center">
          <Grid item xs={2}>
            <Stack direction="row" spacing={1} alignItems="center">
              <Typography>T</Typography>
              <AntSwitch
                checked={checked}
                onChange={handleChangeMode}
                inputProps={{ "aria-label": "ant design" }}
              />
              <Typography>B</Typography>
            </Stack>
          </Grid>
          {!checked ? (
            <Grid item xs={6}>
              <Autocomplete
                options={options}
                value={form}
                onChange={(event, newValue) => {
                  console.log("newValue", newValue);
                  if (newValue) {
                    setForm((old) => ({ ...old, ...newValue }));
                    setFocusState("qty");
                    qtyRef.current.focus();
                  }
                }}
                getOptionLabel={(option) => (option ? option.searchName : "")}
                id="disable-close-on-select"
                inputValue={search}
                onInputChange={handleSearch}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    fullWidth
                    error={false}
                    id="standard-error-helper-text"
                    label="Item"
                    name="name"
                    helperText=""
                    variant="standard"
                  />
                )}
              />
            </Grid>
          ) : (
            <Grid item xs={6}>
              <TextField
                fullWidth
                error={false}
                id="standard-error-helper-text"
                label="Barcode"
                name="barcode"
                inputRef={barRef}
                focused={focusState == "bar" ? true : false}
                helperText=""
                onKeyPress={handleKeyPress}
                onChange={handleChange}
                variant="standard"
                value={form.barcode}
              />
            </Grid>
          )}

          <Grid item xs={2}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Quantity"
              inputRef={qtyRef}
              focused={focusState == "qty" ? true : false}
              name="qty"
              type="number"
              onKeyPress={handleKeyPress}
              onChange={handleChange}
              value={form.qty}
              helperText=""
              variant="standard"
              disabled={form.id ? false : true}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={5}>
        <BillingOps />
      </Grid>
    </Grid>
  );
}
