import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DenseTable from "../shared/table";
import api from "../../utils/api_handler";
import evalJsonata from "../../utils/evanJsonata";
import QueryString from "qs";
import { useAlert } from "react-alert";
import { addToEditor, removeFromEditor } from "../../app/feature/editorSlice";
import {
  Button,
  Grid,
  List,
  ListItem,
  ListItemText,
  MenuItem,
  Paper,
  Stack,
  TablePagination,
  TextField,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import AntSwitch from "../shared/AntSwitch";
import { useDebouncedValue } from "@mantine/hooks";

export default function PurchaseListing({ maxHeight }) {
  let dispatch = useDispatch();
  let key = useSelector((state) => state.editor.key);
  let alert = useAlert();
  let headers = [
    "name",
    "Qty",
    "costPrice",
    "tax",
    "stock",
    "expense",
    "deduction",
    "margin",
    "price",
    "invoice",
  ];

  let [search, setSearch] = useState({
    name: "barcode",
    value: "",
  });

  let [dataS, setDataS] = useState([]);
  let [summary, setSummary] = useState(null);
  let [paging, setPaging] = useState({
    page: 1,
    pageSize: 10,
    total: 0,
  });

  // let sValue = useDebouncedValue(search.value, 200);

  async function loadData() {
    let con = search.value
      ? {
          [search.name]: {
            $containsi: [search.value],
          },
        }
      : {};

    if (search.stock) {
      console.log("stock", search.stock);
      let stock = {
        $gte: 0,
      };
      if (search.stock.from) {
        stock.$gte = search.stock.from;
      }
      if (search.stock.to) {
        stock.$lte = search.stock.to;
      }
      con = { ...con, stock: stock };
    }

    console.log(con);
    let query = QueryString.stringify({
      populate: ["product", "purchase_order"],
      sort: ["createdAt:desc"],
      pagination: paging,
      filters: con,
      summary: 1,
    });

    let {
      status,
      data: dt,
      msg,
    } = await api(`/items?${query}`, "get", null, true);

    console.log("purchase", status, dt);
    if (status) {
      console.log(dt.data.map((e) => ({ id: e.id, ...e.attributes })));
      let list = evalJsonata(
        dt,
        `data.$.{
        "id":id,
        "name":attributes.name,
        "stock":attributes.stock,
        "Qty":attributes.purchaseQty,
        "costPrice":attributes.costPrice,
        "margin":attributes.margin,
        "tax":attributes.tax,
        "expense":attributes.expense,
        "deduction":attributes.deduction,
        "price":attributes.price,
        "invoice":**.purchase_order.**.invoiceNo
        }`
      );

      if (typeof list === "object" && !Array.isArray(list) && list !== null) {
        list = [list];
      }
      setDataS(list);
      setPaging((old) => ({ ...old, total: dt.meta.pagination.total }));
      setSummary(dt.meta.summary);
    } else {
      setDataS([]);
      setPaging({
        page: 1,
        pageSize: 10,
        total: 0,
      });
      setSummary(null);
    }
  }

  useEffect(
    (_) => {
      loadData();
    },
    [key, paging.page]
  );

  // let [timer, setTimer] = useState(null);
  // useEffect(
  //   (_) => {
  //     clearTimeout(timer);
  //     setTimer(
  //       setTimeout((_) => {
  //         loadData();
  //       }, 500)
  //     );
  //   },
  //   [search.value]
  // );

  const handleDelete = async (e) => {
    console.log(e);
    alert.show("Please do not Delete Item, It might affect the inventory");
    // if (window.confirm(`Do you want to delete ?`)) {
    //   let { status, msg } = api("/products/" + e, "delete", null, true);
    //   if (status) {
    //     alert.success("Deleted");
    //     loadData();
    //   } else {
    //     alert.error(msg);
    //   }
    // }
  };

  const handleRowClick = (e) => {
    console.log("Row Click", e);
    dispatch(addToEditor(e));
  };
  return (
    <Grid container direction={"row"} justifyContent="center">
      <Grid item xs={9}>
        <Box sx={{ height: "60px" }}>
          <Grid
            container
            spacing={1}
            justifyContent="space-between"
            alignContent="center"
          >
            <Grid item xs={2}>
              <TextField
                select
                label="select search"
                value={search.name}
                onChange={({ target }) =>
                  setSearch((old) => ({
                    ...old,
                    name: target.value,
                  }))
                }
                variant="standard"
                fullWidth
              >
                <MenuItem value={"barcode"}>Barcode</MenuItem>
                <MenuItem value={"searchName"}>Name</MenuItem>
              </TextField>
            </Grid>

            <Grid item xs={4}>
              <TextField
                label={search.name}
                value={search.value}
                onChange={({ target }) =>
                  setSearch((old) => ({ ...old, value: target.value }))
                }
                variant="standard"
                fullWidth
              />
            </Grid>

            <Grid item xs={1}>
              <TextField
                label="Stock from"
                type="number"
                value={search.stock && search.stock.from}
                onChange={({ target }) =>
                  setSearch((old) => ({
                    ...old,
                    stock: {
                      ...old.stock,
                      from: target.value,
                    },
                  }))
                }
                variant="standard"
                fullWidth
              />
            </Grid>

            <Grid item xs={1}>
              <TextField
                label="Stock to"
                type="number"
                value={search.stock && search.stock.to}
                onChange={({ target }) =>
                  setSearch((old) => ({
                    ...old,
                    stock: {
                      ...old.stock,
                      to: target.value,
                    },
                  }))
                }
                variant="standard"
                fullWidth
              />
            </Grid>

            <Grid item xs={1}>
              <Button variant="contained" onClick={loadData}>
                Search
              </Button>
            </Grid>
          </Grid>
        </Box>
        <DenseTable
          maxHeight={maxHeight}
          headers={headers}
          data={dataS ?? []}
          rowClickHandler={handleRowClick}
          deleteHandler={handleDelete}
          page={true}
          pageProps={[paging, setPaging]}
        />
      </Grid>
      <Grid item xs={3}>
        {summary && (
          <>
            <Paper elevation={4} sx={{ padding: 2, margin: 4 }}>
              <Typography sx={{ marginBottom: 1 }} variant="h6">
                Inventory Summary
              </Typography>

              <Typography sx={{ marginBottom: 1 }} variant="body1">
                Purchased Worth : {summary.billSummary[0].purchaseWorth}
              </Typography>

              <Typography sx={{ marginBottom: 1 }} variant="body1">
                Current Worth : {summary.billSummary[0].stockWorth}
              </Typography>
            </Paper>

            <Paper
              elevation={4}
              sx={{
                padding: 2,
                margin: 4,
                background: "orange",
                color: "white",
              }}
            >
              <Typography sx={{ marginBottom: 1 }} variant="h6">
                Out of stock ({summary.OutOfStock.length})
              </Typography>
              <List sx={{ maxHeight: 400 }}>
                {summary.OutOfStock.map((_, idx) => {
                  return (
                    <ListItem sx={{ marginRight: 0 }}>
                      <ListItemText primary={idx + 1 + ". " + _.name} />
                    </ListItem>
                  );
                })}
              </List>

              {/* <Typography sx={{ marginBottom: 1 }} variant="body1">
              Discount : {summary.billSummary[0].discount}
            </Typography>

            <Typography sx={{ marginBottom: 4 }} variant="p">
              Total Sales : {summary.billSummary[0].total}
            </Typography> */}
            </Paper>
          </>
        )}
      </Grid>
    </Grid>
  );
}
