import { configureStore } from "@reduxjs/toolkit";
import barcodeSlice from "./feature/barcodeSlice";
import cartSlice from "./feature/cartSlice";
import editorSlice from "./feature/editorSlice";
import inventorySlice from "./feature/inventorySlice";
import returnsSlice from "./feature/returnsSlice";
import userSlice from "./feature/userSlice";

export default configureStore({
  reducer: {
    returns: returnsSlice,
    barcode: barcodeSlice,
    inventory: inventorySlice,
    editor: editorSlice,
    user: userSlice,
    cart: cartSlice,
  },
});
