// Manage Billing screen
import * as React from "react";
import {
  Button,
  TextField,
  Grid,
  Paper,
  Typography,
  Link,
} from "@mui/material";
import api from "../utils/api_handler";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { login } from "../app/feature/userSlice";
import { useNavigate } from "react-router-dom";

export default function Login() {
  let dispatch = useDispatch();
  let navigate = useNavigate();
  let user = useSelector((state) => state.user.value);

  let [loading, setLoading] = React.useState(false);

  let alert = useAlert();

  let [state, setState] = React.useState({
    identifier: "",
    password: "",
  });

  React.useEffect((_) => {
    if (user) {
      navigate("/bill/new");
    }
  }, []);
  async function handleSubmit(e) {
    setLoading(true);
    e.preventDefault();
    let { status, data, msg } = await api("/auth/local", "post", state);
    if (status) {
      alert.success("Login success full");
      console.log("data", data);
      dispatch(login(data));
      navigate("/bill/new");
    } else {
      alert.error(msg);
    }
    setLoading(false);
  }

  return (
    <div>
      <Grid container spacing={0} justifyContent="center" direction="row">
        <Grid item md={4}>
          <Grid
            container
            direction="column"
            justifyContent="center"
            spacing={2}
            className="login-form"
          >
            <Paper
              variant="elevation"
              elevation={9}
              className="login-background"
            >
              <Grid item>
                <Typography
                  component="h1"
                  variant="h5"
                  style={{ marginBottom: "22px" }}
                >
                  Sign in
                </Typography>
              </Grid>
              <Grid item>
                <form onSubmit={handleSubmit}>
                  <Grid container direction="column" spacing={2}>
                    <Grid item>
                      <TextField
                        type="text"
                        placeholder="Username"
                        fullWidth
                        name="identifier"
                        variant="outlined"
                        value={state.identifier}
                        onChange={(event) =>
                          setState((old) => ({
                            ...old,
                            [event.target.name]: event.target.value,
                          }))
                        }
                        required
                        autoFocus
                      />
                    </Grid>
                    <Grid item>
                      <TextField
                        type="password"
                        placeholder="Password"
                        fullWidth
                        name="password"
                        variant="outlined"
                        value={state.password}
                        onChange={(event) =>
                          setState((old) => ({
                            ...old,
                            [event.target.name]: event.target.value,
                          }))
                        }
                        required
                      />
                    </Grid>
                    <Grid item>
                      <Button
                        disabled={loading}
                        variant="contained"
                        color="primary"
                        type="submit"
                        className="button-block"
                      >
                        {loading ? "Verifying " : "Login"}
                      </Button>
                    </Grid>
                  </Grid>
                </form>
              </Grid>
            </Paper>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}
