import { createSlice } from "@reduxjs/toolkit";

export const userSlice = createSlice({
  name: "user",
  initialState: {
    value: localStorage.getItem("_userdata")
      ? JSON.parse(window.atob(localStorage.getItem("_userdata")))
      : null,
  },
  reducers: {
    login: (state, action) => {
      console.log(action);
      let data = JSON.stringify(action.payload);
      localStorage.setItem("_userdata", window.btoa(data));
      state.value = action.payload;
    },
    logout: (state) => {
      localStorage.removeItem("_userdata");
      state.value = null;
    },
  },
});

// Action creators are generated for each case reducer function
export const { login, logout } = userSlice.actions;

export default userSlice.reducer;
