import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DenseTable from "../shared/table";
import api from "../../utils/api_handler";
import evalJsonata from "../../utils/evanJsonata";
import QueryString from "qs";
import { useAlert } from "react-alert";
import { addToEditor, removeFromEditor } from "../../app/feature/editorSlice";
import { Button, Grid, TextField } from "@mui/material";
import { Box } from "@mui/system";
import ServerSelect from "../shared/ApiSelect";

export default function PurchaseListing({ maxHeight }) {
  let dispatch = useDispatch();
  let key = useSelector((state) => state.editor.key);
  let alert = useAlert();
  let headers = [
    "invoiceNo",
    "date",
    "subTotal",
    "tax",
    "total",
    "discount",
    "payable",
    "expense",
    "items",
    "supplier",
  ];

  let [dataS, setDataS] = useState([]);

  let [search, setSearch] = useState({
    invoiceNo: "",
    supplier: "",
  });

  let [paging, setPaging] = useState({
    page: 1,
    pageSize: 10,
    total: 0,
  });
  async function loadData() {
    let filters = {};

    console.log("search", search);
    if (search.invoiceNo) {
      filters = {
        invoiceNo: {
          $containsi: search.invoiceNo,
        },
      };
    }

    if (search.supplier) {
      filters = {
        ...filters,
        supplier: {
          id: {
            $eq: search.supplier.id,
          },
        },
      };
    }

    console.log("filters", filters);
    let cond = {
      populate: ["items", "supplier"],
      filters: filters,
      sort: ["createdAt:desc"],
      pagination: paging,
    };
    let query = QueryString.stringify(cond);
    let {
      status,
      data: dt,
      msg,
    } = await api(`/purchase-orders?${query}`, "get", null, true);
    console.log("purchase", status, dt);
    if (status) {
      console.log(dt.data.map((e) => ({ id: e.id, ...e.attributes })));
      let list = evalJsonata(
        dt,
        `data.$.{
        "id":id,
        "invoiceNo":attributes.invoiceNo,
        "expense":attributes.expense ? attributes.expense : "",
        "date":attributes.date ? attributes.date : "",
        "subTotal":attributes.subTotal? attributes.subTotal : "",
        "tax":attributes.tax? attributes.tax : "",
        "discount":attributes.discount? attributes.discount : "",
        "payable":attributes.payable? attributes.payable : "",
        "total":attributes.total? attributes.total : "",
        "items":$count(**.items.**.id),
        "supplier":**.supplier.**.name
        }`
      );

      if (typeof list === "object" && !Array.isArray(list) && list !== null) {
        list = [list];
      }
      setDataS(list);
      setPaging((old) => ({ ...old, total: dt.meta.pagination.total }));
    } else {
      setDataS([]);
      setPaging({
        page: 1,
        pageSize: 10,
        total: 0,
      });
    }
  }

  useEffect(
    (_) => {
      loadData();
    },
    [key, paging.page]
  );

  const handleDelete = async (e) => {
    console.log(e);
    alert.show("Please do not Delete products, It might affect the inventory");
    // if (window.confirm(`Do you want to delete ?`)) {
    //   let { status, msg } = api("/products/" + e, "delete", null, true);
    //   if (status) {
    //     alert.success("Deleted");
    //     loadData();
    //   } else {
    //     alert.error(msg);
    //   }
    // }
  };

  const handleRowClick = (e) => {
    console.log("Row Click", e);
    dispatch(addToEditor(e));
  };

  const handleChange = ({ target }) => {
    console.log("targeting", target);
    setSearch((old) => ({ ...old, [target.name]: target.value }));
  };

  return (
    <>
      <Box sx={{ height: "80px" }}>
        <Grid container alignItems="center">
          <Grid item xs={2}>
            <Button
              variant="contained"
              onClick={(_) => dispatch(addToEditor("new"))}
            >
              New
            </Button>
          </Grid>

          <Grid item xs={4}>
            <TextField
              label="Invoice No"
              name="invoiceNo"
              value={search.invoiceNo}
              onChange={handleChange}
              variant="standard"
            />
          </Grid>
          <Grid item xs={4}>
            <ServerSelect
              name="supplier"
              handleChange={handleChange}
              value={search}
              path="/suppliers"
              hideAdd
            />
          </Grid>
          <Grid item xs={2}>
            <Button variant="contained" onClick={(_) => loadData()}>
              Search
            </Button>
          </Grid>
        </Grid>
      </Box>
      <DenseTable
        colWidth="auto"
        maxHeight={maxHeight}
        headers={headers}
        data={dataS ?? []}
        rowClickHandler={handleRowClick}
        deleteHandler={handleDelete}
        page={true}
        pageProps={[paging, setPaging]}
      />
    </>
  );
}
