import * as React from "react";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import { styled } from "@mui/material/styles";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TablePagination from "@mui/material/TablePagination";
import TableRow from "@mui/material/TableRow";
import { IconButton } from "@mui/material";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { Delete } from "@mui/icons-material";

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  "&:last-child td, &:last-child th": {
    border: 0,
  },
}));

export default function StickyHeadTable({
  maxHeight,
  data,
  headers = null,
  rowClickHandler,
  deleteHandler,
  colWidth,
  moreInfo,
  MoreInfoComponent,
  page,
  pageProps,
}) {
  let cols = headers
    ? headers
    : data
    ? Object.keys(data[0])
    : ["name", "address"];
  let rec = data;

  let [paging, setPaging] = [null, null];

  if (page) {
    [paging, setPaging] = pageProps;
  }

  return (
    <Paper elevation={4} sx={{ width: "100%", overflow: "hidden" }}>
      <TableContainer className="tableMy" sx={{ maxHeight: maxHeight }}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <StyledTableRow>
              <StyledTableCell
                sx={{
                  background: "darkblue",
                  color: "white",
                  fontWeight: "700",
                  width: "35px",
                  padding: "16px 2px 16px 16px",
                }}
                key={"sn"}
              >
                #
              </StyledTableCell>

              {cols.map((column, idx) => (
                <StyledTableCell
                  sx={{
                    background: "darkblue",
                    color: "white",
                    fontWeight: "700",
                    textAlign: "center",
                    minWidth: idx == 0 ? (colWidth == "auto" ? 0 : 250) : 0,
                  }}
                  key={column + "" + idx}
                >
                  {column.replace(/([a-z])([A-Z])/g, "$1 $2").toUpperCase()}
                </StyledTableCell>
              ))}
            </StyledTableRow>
          </TableHead>
          <TableBody>
            {rec.map((row, ridx) => {
              return (
                <StyledTableRow
                  hover
                  onClick={(_) => rowClickHandler(row.id)}
                  role="checkbox"
                  tabIndex={-1}
                  key={row.id + "row"}
                >
                  <StyledTableCell className="tableSNo" key={ridx}>
                    <span className="idx">{ridx + 1}</span>
                    <span className="del">
                      <IconButton
                        color="error"
                        sx={{ padding: "0" }}
                        id={row.id}
                        onClick={(_) => deleteHandler(row.id)}
                      >
                        <Delete color="danger" />
                      </IconButton>
                    </span>
                  </StyledTableCell>
                  {cols.map((column, idx) => {
                    let value = row[column];

                    if (column.match(/At$/gm)) {
                      let dt = new Date(value);
                      value = dt.toLocaleString();
                    }

                    if (column.match(/date$/gm)) {
                      let dt = new Date(value);
                      value = dt.toLocaleDateString();
                    }

                    if (idx == 0 && moreInfo) {
                      return (
                        <Tooltip
                          title={<MoreInfoComponent id={row.id} />}
                          placement="top"
                          key={idx + "v" + row.id}
                        >
                          <StyledTableCell
                            sx={{
                              textAlign: Number(value) >= 0 ? "right" : "left",
                            }}
                          >
                            {value}
                          </StyledTableCell>
                        </Tooltip>
                      );
                    }
                    return (
                      <StyledTableCell
                        key={idx + "v" + row.id}
                        sx={{
                          textAlign: Number(value) >= 0 ? "right" : "left",
                        }}
                      >
                        {value}
                      </StyledTableCell>
                    );
                  })}
                </StyledTableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      {page && (
        <TablePagination
          rowsPerPageOptions={[10, 25, 100]}
          component="div"
          count={paging.total}
          rowsPerPage={paging.pageSize}
          page={paging.page - 1}
          onPageChange={(event, newPage) => {
            setPaging((old) => ({ ...old, page: newPage + 1 }));
          }}
          onRowsPerPageChange={(e) => {
            setPaging({
              page: 1,
              pageSize: e.target.value,
            });
          }}
        />
      )}
    </Paper>
  );
}
