import { Button, Grid, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import QueryString from "qs";
import React from "react";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { addToEditor, setkey } from "../../app/feature/editorSlice";
import api from "../../utils/api_handler";
import evalJsonata from "../../utils/evanJsonata";
import ServerSelect from "../shared/ApiSelect";

const Wrap = ({ children }) => {
  return (
    <Grid item xs={12}>
      {children}
    </Grid>
  );
};

export default function ProductForm() {
  let alert = useAlert();
  let [value, setValue] = React.useState({});
  let dispatch = useDispatch();
  let editor = useSelector((state) => state.editor.value);

  const handleChange = ({ target }) => {
    setValue((old) => ({
      ...old,
      [target.name]: target.value,
    }));
  };

  React.useEffect(
    (_) => {
      if (value.brand || value.size || value.color || value.category) {
        let name =
          (value.brand ? value.brand.name : "") +
          " " +
          (value.color ? value.color.name : "") +
          " " +
          (value.category ? value.category.name : "") +
          " " +
          (value.size ? value.size.name : "") +
          " ";

        setValue((old) => ({ ...old, name: name }));
      }

      return;
    },
    [value.brand, value.size, value.color, value.category]
  );

  async function submitForm() {
    console.log(value);

    let path = "/products";
    let method = "post";
    let suc_msg = "Create Successfully";

    if (editor && editor != "new") {
      path = "/products/" + editor;
      method = "put";
      suc_msg = "Updated Successfullt";
    }

    let data = Object.entries(value).reduce((agg, [k, v]) => {
      if (typeof v === "object" && !Array.isArray(v) && v !== null) {
        agg[k] = v.id;
        return agg;
      }
      agg[k] = v;
      return agg;
    }, {});

    let {
      status,
      data: dt,
      err,
      msg,
    } = await api(path, method, { data: data }, true);

    if (status) {
      alert.success(suc_msg);
      dispatch(setkey());
      setValue({
        name: "",
      });
      dispatch(addToEditor(null));
    } else {
      console.log("err", err);
      msg = evalJsonata(
        err,
        `**.details.errors.$.{
        "msg": $join(**.path)  & " | " & **.message
    }.$join(**.msg)`
      );

      alert.error(msg);
    }
  }

  const fetchFormData = async (id) => {
    let query = QueryString.stringify({
      populate: ["category", "brand", "size", "color"],
    });

    let { status, data, msg } = await api(
      `/products/${id}?${query}`,
      "get",
      null,
      true
    );
    if (status) {
      console.log("Fetched Product", data);
      let details = evalJsonata(
        data,
        `data.$.{
          "id":id,
          "name":attributes.name,
          "brand":**.brand.$.{
            "id":**.id,
            "name":**.attributes.name
          },
          "color":**.color.$.{
            "id":**.id,
            "name":**.attributes.name
          },
          "size":**.size.$.{
            "id":**.id,
            "name":**.attributes.name
          },
          "category":**.category.$.{
            "id":**.id,
            "name":**.attributes.name
          }
          }`
      );
      setValue(details);
      // dispatch(addToEditor(null));
    }
  };

  React.useEffect(
    (_) => {
      if (editor == "new") {
        setValue({
          name: "",
        });
      } else {
        if (editor != null) {
          fetchFormData(editor);
        }
      }
    },
    [editor]
  );

  return (
    <Grid container spacing={3}>
      <Wrap>
        <Typography variant="h6" textAlign={"center"}>
          {editor && editor != "new" ? "Edit " + value["name"] : "Create New"}
        </Typography>
      </Wrap>
      <Wrap>
        <TextField
          label="Name"
          name="name"
          InputProps={{
            readOnly: true,
          }}
          fullWidth
          focused={true}
          value={value ? value["name"] : ""}
          variant="standard"
          onChange={handleChange}
        />
      </Wrap>

      <Wrap>
        <ServerSelect
          name={"brand"}
          handleChange={handleChange}
          value={value}
          path="/brands"
        />
      </Wrap>

      <Wrap>
        <ServerSelect
          name={"size"}
          path="/sizes"
          handleChange={handleChange}
          value={value}
        />
      </Wrap>

      <Wrap>
        <ServerSelect
          name={"color"}
          handleChange={handleChange}
          value={value}
          path="/colors"
        />
      </Wrap>

      <Wrap>
        <ServerSelect
          name={"category"}
          handleChange={handleChange}
          value={value}
          path="/categories"
        />
      </Wrap>
      <Wrap>
        <Button fullWidth variant="contained" onClick={submitForm}>
          {editor && editor != "new" ? "UPDATE" : "CREATE"}
        </Button>
      </Wrap>
    </Grid>
  );
}
