import { createSlice } from "@reduxjs/toolkit";

function financial(x) {
  return Number.parseFloat(x).toFixed(2);
}

function objCalc(state, exCart) {
  state.subTotal = financial(
    Object.values(exCart).reduce((agg, e) => (agg += +e["total"]), 0)
  );

  state.taxTotal = financial(
    Object.values(exCart).reduce((agg, e) => (agg += +e["tax Amt"]), 0)
  );

  state.totalRate = financial(
    Object.values(exCart).reduce((agg, e) => (agg += +e["rate"] * +e["qty"]), 0)
  );

  state.total = financial(+state.subTotal - +state.discount);

  state.maxDiscount = financial(+state.total - +state.totalRate);

  if (!state.locked) {
    localStorage.setItem("_cart", JSON.stringify(exCart));
  }
}

let initState = {
  locked: false,
  printed: localStorage.getItem("_billPrinted") ?? false,
  stage: null,
  id: localStorage.getItem("_billId") ?? null,
  subTotal: "0.00",
  taxTotal: "0.00",
  method: "",
  bill: null,
  total: "0.00",
  discount: "0.00",
  maxDiscount: "0.00",
  value: localStorage.getItem("_cart")
    ? JSON.parse(localStorage.getItem("_cart"))
    : {},
};

// objCalc();
export const cartSlice = createSlice({
  name: "cart",
  initialState: initState,
  reducers: {
    addToCart: (state, action) => {
      let staged = action.payload;

      console.log("stageing", staged);

      staged["total (No Tax)"] = financial(
        (+staged.rate ?? 0) * (+staged.qty ?? 1)
      );

      staged["tax Amt"] = financial(
        +staged["costPrice"] * ((+staged["tax"] ?? 0) / 100)
      );

      staged["total"] = financial(+staged["price"] * (+staged.qty ?? 1));
      staged["amount"] = financial(+staged["price"] * (+staged.qty ?? 1));

      let exCart = { ...state.value };
      exCart[staged.id] = staged;

      state.value = exCart;

      objCalc(state, exCart);
    },
    removeFromCart: (state, action) => {
      let exCart = { ...state.value };
      delete exCart[action.payload];
      state.value = exCart;

      objCalc(state, exCart);
    },
    loadCart: (state, action) => {
      console.log("loadCart", action.payload);
      state.id = action.payload.bill.id;
      state.bill = action.payload.bill;
      state.total = action.payload.bill.total;
      state.discount = action.payload.bill.discount;

      let cartObj = action.payload.cart.reduce((agg, each) => {
        each["total (No Tax)"] = financial(
          (+each.rate ?? 0) * (+each.qty ?? 1)
        );

        each["tax Amt"] = financial(
          +each["costPrice"] * ((+each["tax"] ?? 0) / 100)
        );

        each["total"] = financial(+each["price"] * (+each.qty ?? 1));
        each["amount"] = financial(+each["price"] * (+each.qty ?? 1));

        agg[each.id] = { ...each };
        return agg;
      }, {});
      state.value = cartObj;

      state.locked = action.payload.locked;
      state.locked = true;
      localStorage.setItem("_billId", state.id);
      objCalc(state, state.value);
    },
    reset: (state) => {
      localStorage.removeItem("_cart");
      localStorage.removeItem("_billId");
      localStorage.removeItem("_billPrinted");
      state.value = {};
      state.total = 0;
      state.discount = 0;
      state.method = "cash";
      state.id = null;
      state.printed = false;
      state.locked = false;
      objCalc(state, {});
    },
    addtoStage: (state, action) => {
      state.stage = action.payload;
    },
    removeFromStage: (state) => {
      state.stage = null;
    },
    triggerCalc: (state) => {
      objCalc(state, state.value);
    },
    setMethod: (state, action) => {
      state.method = action.payload;
    },
    setDiscount: (state, action) => {
      state.discount = action.payload;
      objCalc(state, state.value);
    },
    setBillNo: (state, action) => {
      state.id = action.payload;
      localStorage.setItem("_billId", action.payload);
    },
    setPrinted: (state, action) => {
      state.printed = true;
      localStorage.setItem("_billPrinted", 1);
    },
  },
});

// Action creators are generated for each case reducer function
export const {
  addToCart,
  addtoStage,
  removeFromStage,
  removeFromCart,
  loadCart,
  reset,
  triggerCalc,
  setDiscount,
  setBillNo,
  setPrinted,
  setMethod,
} = cartSlice.actions;

export default cartSlice.reducer;
