export default function BillingOps() {
  let cart = useSelector((state) => state.barcode);
  let ref = useRef();
  let nav = useNavigate();
  const alert = useAlert();
  let dispatch = useDispatch();
  async function createBill() {
    // create a bill
    console.log("cartData", cart);
    if (!cart.id) {
      let { subTotal, taxTotal, method, total, discount, value, totalRate } =
        cart;
      let billData = {
        data: {
          subTotal: subTotal,
          taxTotal: taxTotal,
          method: method,
          total: total,
          discount: discount,
          costTotal: totalRate,
        },
      };

      let { status, data, msg } = await api("/bills", "post", billData, true);

      console.log("Bill", status, data);
      if (status) {
        dispatch(setBillNo(data.data.id));
        let billItems = Object.values(value).map((_) => {
          return {
            data: {
              item: _.id,
              bill: data.data.id,
              qty: _.qty,
              rate: _.rate,
              tax: _.tax,
              costTotal: _.costTotal,
            },
          };
        });

        for (let index = 0; index < billItems.length; index++) {
          const billItem = billItems[index];
          let { status, data, msg } = await api(
            "/bill-items",
            "post",
            billItem,
            true
          );
          console.log("BillItems", status);
        }

        nav("/bill/" + data.data.id);
        alert(`Bill #${data.data.id} Created Successfully`);
      }
    }
  }

  function resetHandle() {
    if (!cart.id) {
      if (window.confirm("Do You want to reset ?")) {
        dispatch(reset());
      }
    } else {
      if (cart.printed) {
        dispatch(reset());
      } else {
        alert("Please Print First");
      }
    }
  }

  function handlePrint() {
    var printPreview = window.open("about:blank", "print_preview");
    var printDocument = printPreview.document;
    printDocument.open();
    printDocument.write(
      "<!DOCTYPE html><html><head>" +
        printStyle +
        "</head><body>" +
        document.getElementById("billPrint").outerHTML +
        "</body></html>"
    );

    printPreview.focus();
    printPreview.print();
    printDocument.close();
    // dispatch(setPrinted());
  }

  return (
    <Box
      sx={{
        display: "flex",
        direction: "column",
        justifyContent: "space-around",
      }}
    >
      <Button
        variant="contained"
        disabled={Object.keys(cart.value) <= 0}
        onClick={handlePrint}
        color="primary"
      >
        Print Barcodes
      </Button>

      <Box sx={{ display: "none" }}>
        <BillPrintTemplate ref={ref} />
      </Box>
    </Box>
  );
}
