// Manage Billing screen
import { Button, Grid, Paper, TextField, Typography } from "@mui/material";
import { blue, red } from "@mui/material/colors";
import { Box } from "@mui/system";
import * as React from "react";
import BillingFooter from "../components/returns/footer";
import Stage from "../components/returns/stage";
import BillList from "../components/returns/table";
import ProtectedRoute from "../utils/protectedRoute";
import { useLocation, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import QueryString from "qs";
import api from "../utils/api_handler";
import evalJsonata from "../utils/evanJsonata";
import { loadCart, reset, setBillData } from "../app/feature/returnsSlice";

function Page() {
  let { id } = useParams();
  let returns = useSelector((state) => state.returns);
  let [bill, setBill] = React.useState("");

  let dispatch = useDispatch();

  let [loaded, setLoaded] = React.useState({
    status: false,
    msg: "Fetching Purchase Order",
  });

  const fetchPurchase = async () => {
    let query = QueryString.stringify({
      populate: {
        return_items: {
          populate: ["item"],
        },
        bill: {
          populate: "*",
        },
      },
    });

    let { status, data, msg } = await api(
      `/returns/${id}?${query}`,
      "get",
      null,
      true
    );

    if (status) {
      console.log("bill", data);

      let items = evalJsonata(
        data,
        `data.**.return_items.data.$.{
          "id":**.item.**.id,
          "name":**.item.**.name,
          "costPrice":**.costPrice,
           "margin":**.margin,
           "qty":**.qty,
           "price":**.price,
           "rate":**.item.**.rate,
           "taxedCostPrice":**.taxedCostPrice,
           "tax":**.item.**.tax
      }`
      );

      items = items && !Array.isArray(items) ? [items] : items ?? [];

      console.log("items", items);
      dispatch(
        loadCart({
          id: id,
          bill: evalJsonata(
            data,
            `data.$.{
            "id":id,
            "billNo":**.bill.data.id,
            "invoiceNo":attributes.id,
            "expense":attributes.expense ? attributes.expense : "",
            "date":attributes.date ? attributes.date : "",
            "subTotal":attributes.subTotal? attributes.subTotal : "",
            "tax":attributes.tax? attributes.tax : "",
            "method":attributes.method? attributes.method : "",
            "total":attributes.total? attributes.total : "",
            "items":$count(**.items.**.id),
            "supplier":**.supplier.**.name
            }`
          ),
          cart: items ?? [],
          locked: true,
        })
      );
      setLoaded({
        status: true,
        msg: msg,
        modal: false,
      });
    } else {
      setLoaded({
        status: false,
        msg: msg,
        modal: false,
      });
    }
  };

  async function loadBill() {
    let query = QueryString.stringify({
      populate: {
        bill_items: {
          populate: ["item"],
        },
      },
    });

    let { status, data, msg } = await api(
      `/bills/${bill}?${query}`,
      "get",
      null,
      true
    );

    if (status) {
      console.log("bill", data);

      let items = evalJsonata(
        data,
        `data.**.bill_items.data.$.{
          "id":**.item.**.id,
           "qty":**.qty
           
      }`
      );

      items = items && !Array.isArray(items) ? [items] : items ?? [];

      console.log("items", items);
      dispatch(
        setBillData({
          id: data.data.id,
          items: items,
        })
      );
      setLoaded({
        status: true,
        modal: false,
      });
    } else {
      setLoaded({
        status: false,
        msg: msg,
        modal: false,
      });
    }
  }

  React.useEffect(
    (_) => {
      dispatch(reset());
      console.log("Location", id);
      if (id && id != "new") {
        console.log("here");
        fetchPurchase();
      } else {
        setLoaded({
          status: true,
          modal: true,
        });
      }
    },
    [id]
  );

  if (!loaded.status) {
    return (
      <Paper
        elevation={7}
        sx={{
          margin: "20vh auto",
          width: 300,
          padding: 4,
          background: "Orange",
          color: "white",
        }}
      >
        <Typography variant="h6">{loaded.msg}</Typography>
      </Paper>
    );
  }

  if (loaded.modal) {
    return (
      <Paper
        elevation={7}
        sx={{
          margin: "20vh auto",
          width: 200,
          padding: 4,
          background: "beige",
          color: "white",
        }}
      >
        <TextField
          label="Enter Bill No"
          sx={{ marginBottom: "20px" }}
          type="text"
          value={bill}
          onChange={({ target }) => setBill(target.value)}
          variant="standard"
          fullWidth
        />
        <Button fullWidth onClick={loadBill} variant="contained" fullwidth>
          Load Bill
        </Button>
      </Paper>
    );
  }

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",

        height: "calc(100vh - 48px)",
        padding: "12px 16px",
      }}
    >
      <Box sx={{ height: "80px" }}>
        <Stage />
      </Box>

      <Box sx={{ height: "calc(100vh - 290px)" }}>
        <BillList maxHeight={"calc(100vh - 290px)"} />
      </Box>
      <Box sx={{ height: "100px" }}>
        <BillingFooter />
      </Box>
    </Box>
  );
}

export default function ReturnScreen() {
  return (
    <ProtectedRoute>
      <Page />
    </ProtectedRoute>
  );
}
