import { Grid, Paper, TextField, Typography } from "@mui/material";
import React, { useEffect } from "react";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { setDiscount, triggerCalc } from "../../app/feature/inventorySlice";
import PurchaseOps from "./ops";

export default function BillingFooter() {
  let { invoiceNo, subTotal, discount, total, expense, payable, tax } =
    useSelector((state) => state.inventory.purchaseOrder);
  let [msg, setMsg] = React.useState("");

  let { billTotal } = useSelector((state) => state.inventory);
  let dispatch = useDispatch();
  useEffect((_) => dispatch(triggerCalc()), []);

  useEffect(
    (_) => {
      if (Number(billTotal) != (total ? Number(total) : 0)) {
        setMsg("Bill Amount  and Entry total not equal ");
      } else {
        setMsg("All good");
      }
    },
    [billTotal, total]
  );
  return (
    <Grid container direction="row" spacing={2} justifyContent="space-between">
      <Grid item xs={3}>
        <Paper
          elevation={8}
          sx={{
            padding: "20px",
            background:
              Number(billTotal) == (total ? Number(total) : 0)
                ? "darkgreen"
                : "darkred",
            color: "beige",
          }}
        >
          <Typography variant="subtitle1">{msg}</Typography>

          <Typography sx={{ fontSize: "48px" }}>
            ₹ {Math.round(billTotal).toFixed(2)}
          </Typography>
        </Paper>
      </Grid>
      <Grid item xs={6}>
        <Grid container spacing={2} justifyContent="flex-end">
          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Invoice No"
              value={invoiceNo ? invoiceNo : ""}
              name="barcode"
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Expense"
              name="expense"
              value={expense ? expense.toFixed(2) : ""}
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Sub Total"
              name="subtotal"
              value={subTotal ? subTotal.toFixed(2) : ""}
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label={`Total @ ${tax} % Tax`}
              value={total ? total.toFixed(2) : ""}
              name="barcode"
              helperText=""
              variant="standard"
            />
          </Grid>
          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Discount"
              value={discount ? discount.toFixed(2) : ""}
              name="barcode"
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Total"
              value={payable ? payable.toFixed(2) : ""}
              name="barcode"
              helperText=""
              variant="standard"
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={3}>
        <PurchaseOps />
      </Grid>
    </Grid>
  );
}
