import { createSlice } from "@reduxjs/toolkit";

export const editorSlice = createSlice({
  name: "editor",
  initialState: {
    key: null,
    type: "",
    value: null,
  },
  reducers: {
    addToEditor: (state, action) => {
      state.value = action.payload;
    },
    removeFromEditor: (state) => {
      state.value = null;
    },
    setkey: (state) => {
      state.key = Date.now();
    },
  },
});

// Action creators are generated for each case reducer function
export const { addToEditor, removeFromEditor, setkey } = editorSlice.actions;

export default editorSlice.reducer;
