import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import AccountCircle from "@mui/icons-material/AccountCircle";
import Switch from "@mui/material/Switch";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormGroup from "@mui/material/FormGroup";
import MenuItem from "@mui/material/MenuItem";
import MenuList from "../components/menu";

import { useDispatch, useSelector } from "react-redux";
import { NavLink, useLocation } from "react-router-dom";
import { Drawer, Menu } from "@mui/material";
import { logout } from "../app/feature/userSlice";
import { cfLetter } from "../utils";
import { removeFromEditor } from "../app/feature/editorSlice";

export default function MenuAppBar() {
  let user = useSelector((state) => state.user.value);
  let bill = useSelector((state) => state.cart.bill);
  let returnBilllNo = useSelector((state) => state.returns.billNo);

  let location = useLocation();
  let [title, setTitle] = React.useState("");
  let [open, toggleDrawer] = React.useState(false);

  let dispatch = useDispatch();
  const [auth, setAuth] = React.useState(true);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleChange = (event) => {
    setAuth(event.target.checked);
  };

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const logoutHandle = () => {
    dispatch(logout());
    handleClose();
  };
  React.useEffect(
    (_) => {
      console.log("Version", "1.0.2");

      let name = cfLetter(location.pathname.split("/")[1]);
      name = location.pathname == "/" ? "Billing" : name;
      name = location.pathname == "/products" ? "Manage Products" : name;
      name = location.pathname == "/login" ? "Login" : name;
      name = location.pathname == "/purchase" ? "Purchase Orders" : name;
      name = location.pathname.includes("/bill/")
        ? `Bill No : ${
            bill
              ? bill.id + "     on  " + new Date(bill.date).toLocaleString()
              : ""
          }`
        : name;

      name = location.pathname.includes("/return/")
        ? `Return for Bill No : ${returnBilllNo ? returnBilllNo : ""}`
        : name;
      name = location.pathname == "/bill/new" ? "New Bill" : name;

      setTitle(name);
    },
    [location, bill, returnBilllNo]
  );

  return (
    <>
      <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static">
          <Toolbar variant="dense">
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              onClick={(_) => toggleDrawer(true)}
              sx={{ mr: 2 }}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              {title}
            </Typography>
            {user ? (
              <div>
                <IconButton
                  size="large"
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  onClick={handleMenu}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>
                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  open={Boolean(anchorEl)}
                  onClose={handleClose}
                >
                  <MenuItem onClick={logoutHandle}>Logout</MenuItem>
                </Menu>
              </div>
            ) : (
              <NavLink
                style={{ textDecoration: "none", color: "black" }}
                to={"/login"}
                className={(isActive) =>
                  "nav-link" + (!isActive ? " unselected" : "")
                }
              >
                Login
              </NavLink>
            )}
          </Toolbar>
        </AppBar>
      </Box>
      <>
        <Drawer
          anchor={"left"}
          open={open}
          onClose={(_) => toggleDrawer(false)}
        >
          <MenuList toggleDrawer={toggleDrawer} />
        </Drawer>
      </>
    </>
  );
}
