import {
  Autocomplete,
  Button,
  Grid,
  IconButton,
  Modal,
  Paper,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import QueryString from "qs";

import React, { useEffect } from "react";
import api from "../../utils/api_handler";
import { useDispatch, useSelector } from "react-redux";
import { addToCart, removeFromStage } from "../../app/feature/inventorySlice";
import BillingOps from "./ops";
import { Add } from "@mui/icons-material";

import { addToEditor, removeFromEditor } from "../../app/feature/editorSlice";
import ProductForm from "../Products/form";

const style = {
  position: "absolute",
  background: "beige",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 200,
  p: 4,
};

export default function Stage() {
  let barRef = React.useRef();
  let purchaseQtyRef = React.useRef();
  let cstRef = React.useRef();
  let { tax: purchaseTax } = useSelector(
    (state) => state.inventory.purchaseOrder
  );

  let formInit = {
    id: "",
    barcode: "",
    name: "",
    margin: "",
    purchaseQty: "",
    costPrice: "",
    expenses: "",
    tax: purchaseTax,
  };

  let stage = useSelector((state) => state.inventory.stage);

  const [checked, setChecked] = React.useState(true);
  const [focusState, setFocusState] = React.useState("");
  const [timer, setTimer] = React.useState(null);
  const [options, setOptions] = React.useState([]);
  const [search, setSearch] = React.useState("");
  const handleChangeMode = (event) => {
    setChecked(event.target.checked);
  };

  let [form, setForm] = React.useState(formInit);
  let [modal, setModal] = React.useState(false);

  let dispatch = useDispatch();

  async function handleKeyPress(event) {
    // barcode handle
    if (event.target.name == "barcode") {
      if (event.key === "Enter") {
        let query = QueryString.stringify({
          filters: {
            barcode: {
              $containsi: form.barcode,
            },
          },
        });
        let res = await api(`/items?${query}`, "get", null, true);
        if (res.status) {
          let items = res.data.data.map((e) => ({ id: e.id, ...e.attributes }));
          setForm((old) => ({ ...old, ...items[0] }));

          // setFocusState("cos");
          // purchaseQtyRef.current.focus();
        }
      }
    }

    if (event.target.name == "tax") {
      if (event.key === "Enter") {
        dispatch(addToCart(form));
        setForm(formInit);

        setFocusState("bar");
        barRef.current.focus();
      }
    }
  }

  function handleChange({ target }) {
    setForm((old) => ({ ...old, [target.name]: target.value }));
  }

  function handleSearch(event, newValue) {
    setSearch(newValue);
    clearTimeout(timer);
    let newTimer = setTimeout(async (_) => {
      let query = QueryString.stringify({
        filters: {
          name: {
            $containsi: newValue,
          },
        },
      });
      let res = await api(`/products?${query}`, "get", null, true);
      let items = res.data.data.map((e) => ({ id: e.id, ...e.attributes }));
      setOptions(items);
      console.log(res);
    }, 500);

    setTimer(newTimer);
  }

  useEffect(
    (_) => {
      console.log("staged", stage);
      if (stage) {
        setForm(stage);
        setSearch(stage.name);
        // dispatch(removeFromStage());
      }
    },
    [stage]
  );

  return (
    <Grid
      container
      direction={"row"}
      spacing={2}
      justifyContent="space-between"
      alignItems="center"
    >
      <Grid item xs={12}>
        <Grid container direction={"row"} spacing={2} alignItems="center">
          <Grid item xs={3}>
            <Autocomplete
              options={options}
              value={form}
              onChange={(event, newValue) => {
                console.log("newValue", newValue);
                if (newValue) {
                  setForm((old) => ({ ...old, ...newValue }));
                  // setFocusState("purchaseQty");
                  // purchaseQtyRef.current.focus();
                }
              }}
              getOptionLabel={(option) => (option ? option.name : "")}
              id="disable-close-on-select"
              inputValue={search}
              onInputChange={handleSearch}
              renderInput={(params) => (
                <TextField
                  {...params}
                  fullWidth
                  inputRef={barRef}
                  error={false}
                  id="standard-error-helper-text"
                  label="Item"
                  name="name"
                  helperText=""
                  variant="standard"
                />
              )}
            />
          </Grid>
          <Grid item>
            <IconButton onClick={(_) => setModal(true)}>
              <Add />
            </IconButton>
          </Grid>

          <Grid item xs={2}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Cost Price"
              type="number"
              inputRef={cstRef}
              focused={focusState == "cst" ? true : false}
              name="costPrice"
              onKeyPress={handleKeyPress}
              onChange={handleChange}
              value={form.costPrice}
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={2}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Quantity"
              type="number"
              inputRef={purchaseQtyRef}
              focused={focusState == "purchaseQty" ? true : false}
              name="purchaseQty"
              onKeyPress={handleKeyPress}
              onChange={handleChange}
              value={form.purchaseQty}
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={2}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Margin"
              type="number"
              // inputRef={cstRef}
              // focused={focusState == "cst" ? true : false}
              name="margin"
              onKeyPress={handleKeyPress}
              onChange={handleChange}
              value={form.margin}
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={1}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Tax"
              // inputRef={cstRef}
              // focused={focusState == "cst" ? true : false}
              type="number"
              name="tax"
              onKeyPress={handleKeyPress}
              onChange={handleChange}
              value={form.tax}
              helperText=""
              variant="standard"
            />
          </Grid>
        </Grid>
      </Grid>

      <Modal
        open={modal}
        onClose={(_) => {
          dispatch(removeFromEditor());
          setModal(false);
        }}
      >
        <Paper sx={style} elevation={8}>
          <ProductForm />
        </Paper>
      </Modal>
    </Grid>
  );
}
