import { createSlice } from "@reduxjs/toolkit";

function financial(x) {
  return Number.parseFloat(x).toFixed(2);
}

function objCalc(state, exCart) {
  state.billTotal = financial(
    Object.values(exCart).reduce((agg, e) => (agg += +e["billedAmt"]), 0)
  );

  let { expense: texp, discount: tdis } = state.purchaseOrder;
  let cart = Object.entries(exCart).reduce((agg, [k, v]) => {
    let unitExp =
      financial(texp * (v.billedAmt / state.billTotal)) / v.purchaseQty;
    let unitDis =
      financial(tdis * (v.billedAmt / state.billTotal)) / v.purchaseQty;

    let price = +financial(v.taxedCostPrice) + +unitExp - +unitDis;
    agg[k] = {
      ...v,
      expense: unitExp,
      deduction: unitDis,
      rate: price,
      price: financial(price * (1 + v.margin / 100)),
    };

    return agg;
  }, {});

  console.log("calc", cart);
  state.value = cart;
  state.taxTotal = financial(
    Object.values(exCart).reduce((agg, e) => (agg += +e["tax Amt"]), 0)
  );

  state.total = financial(+state.subTotal + +state.taxTotal - +state.discount);

  localStorage.setItem("_inventory", JSON.stringify(cart));
}

let initState = {
  locked: false,
  printed: localStorage.getItem("_barcodePrinted") ?? false,
  stage: null,
  id: localStorage.getItem("_purchaseId") ?? null,
  purchaseOrder: null,
  billTotal: "0.00",
  taxTotal: "0.00",
  method: "",
  total: "0.00",
  discount: "0.00",
  value: localStorage.getItem("_inventory")
    ? JSON.parse(localStorage.getItem("_inventory"))
    : {},
};

// objCalc();
export const inventorySlice = createSlice({
  name: "inventory",
  initialState: initState,
  reducers: {
    addToCart: (state, action) => {
      let staged = action.payload;

      staged["taxedCostPrice"] = financial(
        (+staged.costPrice ?? 0) * (1 + (+staged.tax ?? 0) / 100)
      );

      staged["billedAmt"] = financial(
        +staged["taxedCostPrice"] * +staged.purchaseQty
      );

      let exCart = { ...state.value };
      exCart[staged.id] = staged;

      state.value = exCart;

      objCalc(state, exCart);
    },
    removeFromCart: (state, action) => {
      let exCart = { ...state.value };
      delete exCart[action.payload];
      state.value = exCart;

      objCalc(state, exCart);
    },
    loadCart: (state, action) => {
      state.id = action.payload.purchaseOrder.id;
      state.purchaseOrder = action.payload.purchaseOrder;

      let cartObj = action.payload.cart.reduce((agg, each) => {
        agg[each.id] = { ...each };
        return agg;
      }, {});
      state.value = cartObj;

      state.locked = action.payload.locked;

      objCalc(state, state.value);
    },
    reset: (state) => {
      localStorage.removeItem("_inventory");
      localStorage.removeItem("_purchaseId");
      localStorage.removeItem("_barcodePrinted");
      state.value = {};
      objCalc(state, {});
      state.id = null;
      state.printed = false;
    },
    addtoStage: (state, action) => {
      state.stage = action.payload;
    },
    removeFromStage: (state) => {
      state.stage = null;
    },
    triggerCalc: (state) => {
      objCalc(state, state.value);
    },
    setDiscount: (state, action) => {
      state.discount = action.payload;
      objCalc(state, state.value);
    },
    setBillNo: (state, action) => {
      state.id = action.payload;
      localStorage.setItem("_purchaseId", action.payload);
    },
    setLocked: (state, action) => {
      state.locked = action.payload;
    },
    setPrinted: (state, action) => {
      state.printed = true;
      localStorage.setItem("_barcodePrinted", 1);
    },
  },
});

// Action creators are generated for each case reducer function
export const {
  addToCart,
  addtoStage,
  removeFromStage,
  removeFromCart,
  loadCart,
  reset,
  triggerCalc,
  setDiscount,
  setBillNo,
  setPrinted,
  setLocked,
} = inventorySlice.actions;

export default inventorySlice.reducer;
