import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DenseTable from "../shared/table";
import api from "../../utils/api_handler";
import evalJsonata from "../../utils/evanJsonata";
import QueryString from "qs";
import { useAlert } from "react-alert";
import { addToEditor, removeFromEditor } from "../../app/feature/editorSlice";
import { Button, Grid, Paper, TablePagination, TextField } from "@mui/material";
import { Box, flexbox } from "@mui/system";
import {
  Add,
  ArrowDownward,
  ArrowUpward,
  Balance,
  BalanceRounded,
  BalanceTwoTone,
} from "@mui/icons-material";
import ServerSelect from "../shared/ApiSelect";
import { orange } from "@mui/material/colors";

export default function PurchaseListing({ maxHeight }) {
  let dispatch = useDispatch();
  let key = useSelector((state) => state.editor.key);
  let alert = useAlert();
  let headers = ["date", "supplier", "credit", "debit", "remark"];

  let [value, setValue] = React.useState({});

  const handleChange = ({ target }) => {
    setValue((old) => ({ ...old, [target.name]: target.value }));
  };

  let [dataS, setDataS] = useState([]);
  let [summary, setSummary] = useState({
    credit: 0,
    debit: 0,
    balance: 0,
  });

  let [paging, setPaging] = useState({
    page: 1,
    pageSize: 10,
    total: 0,
  });

  async function loadData() {
    let query = QueryString.stringify({
      populate: ["supplier"],
      sort: ["createdAt:desc"],
      pagination: paging,
      filters: value.supplier
        ? {
            supplier: {
              id: {
                $eq: value.supplier.id,
              },
            },
          }
        : {},
    });

    let {
      status,
      data: dt,
      msg,
    } = await api(`/supplier-transactions?${query}`, "get", null, true);

    console.log("supplier-transaction", status, dt);
    if (status) {
      console.log(dt.data.map((e) => ({ id: e.id, ...e.attributes })));
      let list = evalJsonata(
        dt,
        `data.$.{
        "id":id,
        "date":attributes.date,
        "supplier":**.supplier.**.name,
        "credit":attributes.credit,
        "debit":attributes.debit,
        "remark":attributes.remark
        }`
      );

      if (typeof list === "object" && !Array.isArray(list) && list !== null) {
        list = [list];
      }
      setDataS(list);
      setPaging((old) => ({ ...old, total: dt.meta.pagination.total }));
      setSummary(dt.meta.summary[0]);
    } else {
      setDataS([]);
      setPaging({
        page: 1,
        pageSize: 10,
        total: 0,
      });
      setSummary({
        credit: 0,
        debit: 0,
        bal: 0,
      });
    }
  }

  useEffect(
    (_) => {
      loadData();
    },
    [key, paging.page, value]
  );

  const handleDelete = async (e) => {
    console.log(e);
    alert.show("Payments cannot be Deleted");
    // if (window.confirm(`Do you want to delete ?`)) {
    //   let { status, msg } = api("/products/" + e, "delete", null, true);
    //   if (status) {
    //     alert.success("Deleted");
    //     loadData();
    //   } else {
    //     alert.error(msg);
    //   }
    // }
  };

  const handleRowClick = (e) => {
    console.log("Row Click", e);
    dispatch(addToEditor(e));
  };
  return (
    <>
      <Box sx={{ height: "80px" }}>
        <Grid container spacing={2} justifyContent="space-between">
          <Grid item xs={1}>
            <Button
              variant="contained"
              onClick={(_) => dispatch(addToEditor("new"))}
            >
              New
            </Button>
          </Grid>

          <Grid item xs={3}>
            <ServerSelect
              name={"supplier"}
              handleChange={handleChange}
              value={value}
              path="/suppliers"
              hideAdd
            />
          </Grid>

          <Grid item xs={2}>
            <Paper
              elevation={4}
              sx={{
                padding: 2,

                fontWeight: 700,
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <ArrowDownward />
              <span>{summary.credit}</span>
            </Paper>
          </Grid>

          <Grid item xs={2}>
            <Paper
              elevation={4}
              sx={{
                padding: 2,

                fontWeight: 700,
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <ArrowUpward />
              <span>{summary.debit}</span>
            </Paper>
          </Grid>

          <Grid item xs={2}>
            <Paper
              elevation={4}
              sx={{
                padding: 2,

                fontWeight: 700,
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <BalanceTwoTone />
              <span>{summary.bal}</span>
            </Paper>
          </Grid>
        </Grid>
      </Box>
      <DenseTable
        colWidth={"auto"}
        maxHeight={maxHeight}
        headers={headers}
        data={dataS ?? []}
        rowClickHandler={handleRowClick}
        deleteHandler={handleDelete}
        page={true}
        pageProps={[paging, setPaging]}
      />
    </>
  );
}
