import { Grid, MenuItem, Paper, TextField, Typography } from "@mui/material";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  setDiscount,
  setMethod,
  triggerCalc,
} from "../../app/feature/cartSlice";

export default function BillingFooter() {
  let { total, subTotal, method, taxTotal, discount, maxDiscount, locked } =
    useSelector((state) => state.cart);
  let dispatch = useDispatch();
  useEffect((_) => dispatch(triggerCalc()), []);

  return (
    <Grid container direction="row" spacing={2} justifyContent="space-between">
      <Grid item xs={3}>
        <Paper
          elevation={8}
          sx={{ padding: "20px", background: "orange", color: "black" }}
        >
          <Typography variant="subtitle1">Maximum Discount</Typography>

          <Typography sx={{ fontSize: "48px" }}>
            ₹ {Math.round(maxDiscount).toFixed(2)}
          </Typography>
        </Paper>
      </Grid>
      <Grid item xs={6}>
        <Grid container spacing={2} justifyContent="flex-end">
          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              value={method}
              id="standard-error-helper-text"
              label="Payment"
              name="barcode"
              helperText=""
              variant="standard"
              select
              onChange={({ target }) => dispatch(setMethod(target.value))}
            >
              <MenuItem value="cash">CASH</MenuItem>
              <MenuItem value="card">CARD</MenuItem>
              <MenuItem value="upi">UPI</MenuItem>
            </TextField>
          </Grid>

          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Sub Total"
              name="barcode"
              value={subTotal.toString()}
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Tax Amount"
              name="barcode"
              value={taxTotal.toString()}
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Discount"
              disabled={locked}
              value={discount}
              name="barcode"
              onChange={({ target }) => dispatch(setDiscount(target.value))}
              helperText=""
              variant="standard"
            />
          </Grid>

          <Grid item xs={4}>
            <TextField
              fullWidth
              error={false}
              id="standard-error-helper-text"
              label="Total"
              value={total}
              name="barcode"
              helperText=""
              variant="standard"
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={3}>
        <Paper
          elevation={8}
          sx={{ padding: "20px", background: "darkblue", color: "beige" }}
        >
          <Typography variant="subtitle1">Total</Typography>

          <Typography sx={{ fontSize: "48px" }}>
            ₹ {Math.round(total).toFixed(2)}
          </Typography>
        </Paper>
      </Grid>
    </Grid>
  );
}
