import React from "react";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { addtoStage, removeFromCart } from "../../app/feature/inventorySlice";
import DenseTable from "../shared/table";

export default function BillList({ maxHeight }) {
  let headers = [
    "name",
    "barcode",
    "costPrice",
    "purchaseQty",
    "tax",
    "taxedCostPrice",
    "billedAmt",
    "expense",
    "deduction",
    "margin",

    "price",
  ];

  let alert = useAlert();
  let { value: CartObj, locked } = useSelector((state) => state.inventory);
  let dispatch = useDispatch();
  console.log("cart", CartObj);
  let data = Object.values(CartObj);

  const handleDelete = (e) => {
    console.log(e);
    if (locked) {
      alert.info("Cannot Edit you have save This");
      return;
    }
    if (window.confirm(`Do you want to delete ${CartObj[e].name} ?`)) {
      dispatch(removeFromCart(e));
    }
  };

  const handleRowClick = (e) => {
    console.log("Row Click", CartObj[e]);

    if (locked) {
      alert.info("Cannot Edit you have save This");
      return;
    }
    dispatch(addtoStage(CartObj[e]));
  };
  return (
    <DenseTable
      colWidth={"auto"}
      maxHeight={maxHeight}
      headers={headers}
      data={data}
      rowClickHandler={handleRowClick}
      deleteHandler={handleDelete}
    />
  );
}
