import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DenseTable from "../shared/table";
import api from "../../utils/api_handler";
import evalJsonata from "../../utils/evanJsonata";
import QueryString from "qs";
import { useAlert } from "react-alert";
import { addToEditor, removeFromEditor } from "../../app/feature/editorSlice";
import {
  Button,
  TablePagination,
  Grid,
  MenuItem,
  Stack,
  TextField,
  Typography,
  Paper,
} from "@mui/material";

import { Box } from "@mui/system";
import { useNavigate } from "react-router-dom";

export default function PurchaseListing({ maxHeight }) {
  let dispatch = useDispatch();
  let nav = useNavigate();
  let key = useSelector((state) => state.editor.key);
  let alert = useAlert();
  let headers = [
    "createdAt",
    "billNo",
    "subTotal",
    "discount",
    "total",
    "items",
  ];

  let [dataS, setDataS] = useState([]);
  let [summary, setSummary] = useState(null);

  let [paging, setPaging] = useState({
    page: 1,
    pageSize: 10,
    total: 0,
  });

  let [search, setSearch] = useState({
    from: new Date().toISOString().split("T")[0] + "T00:01",
    to: new Date().toISOString().split("T")[0] + "T23:59",
    bill: "",
  });

  async function loadData() {
    let criteria = {
      populate: ["bill_items"],
      sort: ["createdAt:desc"],
      pagination: paging,
      filters: {},
    };

    console.log("from", search.from);

    criteria.filters = {
      createdAt: {
        $between: [
          new Date(search.from).toISOString(),
          new Date(search.to).toISOString(),
        ],
      },
    };

    if (search.bill) {
      criteria.filters = {
        ...criteria.filters,
        id: {
          $eq: search.bill,
        },
      };
    }

    let query = QueryString.stringify(criteria);

    let {
      status,
      data: dt,
      msg,
    } = await api(`/bills?${query}`, "get", null, true);

    console.log("purchase", status, dt);
    if (status) {
      console.log(dt.data.map((e) => ({ id: e.id, ...e.attributes })));
      let list = evalJsonata(
        dt,
        `data.$.{
        "id":id,
        "billNo":"B" & id,
        "subTotal":attributes.subTotal,
        "total":attributes.total,
        "discount":attributes.discount,
        "createdAt":attributes.createdAt,
        "date":$fromMillis($toMillis(attributes.createdAt), '[D01]/[M01]/[Y01] [h#1]:[m01][P]'),
        "items":$count(**.bill_items.**.id)
        }`
      );

      if (typeof list === "object" && !Array.isArray(list) && list !== null) {
        list = [list];
      }
      setDataS(list);
      setSummary(evalJsonata(dt, `**.summary`));
      setPaging((old) => ({ ...old, total: dt.meta.pagination.total }));
    } else {
      setDataS([]);
      setSummary(null);
    }
  }

  useEffect(
    (_) => {
      loadData();
    },
    [key, paging.page]
  );

  const handleDelete = async (e) => {
    console.log(e);
    alert.show("Please do not Delete products, It might affect the inventory");
    // if (window.confirm(`Do you want to delete ?`)) {
    //   let { status, msg } = api("/products/" + e, "delete", null, true);
    //   if (status) {
    //     alert.success("Deleted");
    //     loadData();
    //   } else {
    //     alert.error(msg);
    //   }
    // }
  };

  const handleRowClick = (e) => {
    console.log("Row Click", e);
    nav("/bill/" + e);
    dispatch(addToEditor(e));
  };

  let handleChange = ({ target }) => {
    // if (target.name == "from" || target.name == "to") {
    //   target.value = new Date(target.value).toISOString();
    // }
    setSearch((old) => ({
      ...old,
      [target.name]: target.value,
    }));
  };
  return (
    <Grid container direction={"row"} justifyContent="center">
      <Grid item xs={8}>
        <Box sx={{ height: "80px" }}>
          <Grid container spacing={1} alignItems={"center"}>
            <Grid item xs={3}>
              <TextField
                value={search.from}
                type="datetime-local"
                label="From"
                variant="standard"
                name="from"
                onChange={handleChange}
                fullWidth
              />
            </Grid>

            <Grid item xs={3}>
              <TextField
                value={search.to}
                variant="standard"
                label="To"
                type="datetime-local"
                name="to"
                onChange={handleChange}
                fullWidth
              />
            </Grid>

            <Grid item xs={3}>
              <TextField
                value={search.bill}
                label="Bill No"
                variant="standard"
                name="bill"
                onChange={handleChange}
                fullWidth
              />
            </Grid>
            <Grid item xs={3}>
              <Button variant="contained" onClick={(_) => loadData()}>
                Search
              </Button>
            </Grid>
          </Grid>
        </Box>
        <DenseTable
          colWidth={"auto"}
          maxHeight={maxHeight}
          headers={headers}
          data={dataS ?? []}
          rowClickHandler={handleRowClick}
          deleteHandler={handleDelete}
          page={true}
          pageProps={[paging, setPaging]}
        />
      </Grid>

      <Grid item xs={4}>
        {summary && (
          <Paper elevation={4} sx={{ padding: 2, margin: 4 }}>
            <Typography sx={{ marginBottom: 1 }} variant="h6">
              Bill Summary
            </Typography>

            <Typography sx={{ marginBottom: 1 }} variant="body1">
              Cost Total : {summary.billSummary[0].costTotal}
            </Typography>

            <Typography sx={{ marginBottom: 1 }} variant="body1">
              Sub Total : {summary.billSummary[0].subTotal}
            </Typography>
            <Typography sx={{ marginBottom: 1, color: "red" }} variant="body1">
              Discount : {summary.billSummary[0].discount}
            </Typography>

            <Typography sx={{ marginBottom: 1, color: "blue" }} variant="body1">
              Total Sales : {summary.billSummary[0].total}
            </Typography>

            <Typography
              sx={{ marginBottom: 4, color: "darkgreen" }}
              variant="body1"
            >
              Profit :{" "}
              {+summary.billSummary[0].total -
                +summary.billSummary[0].costTotal ?? 0}
            </Typography>

            <hr></hr>
            <Typography sx={{ marginBottom: 1 }} variant="h6">
              Returns Summary
            </Typography>

            <Typography sx={{ marginBottom: 1 }} variant="body1">
              Sub Total : {summary.returnsSummary[0].total}
            </Typography>
          </Paper>
        )}
      </Grid>
    </Grid>
  );
}
