import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { addtoStage, removeFromCart } from "../../app/feature/returnsSlice";
import DenseTable from "../shared/table";

export default function BillList({ maxHeight }) {
  let headers = ["name", "price", "qty", "amount"];

  let CartObj = useSelector((state) => state.returns.value);
  let dispatch = useDispatch();
  console.log("returns", CartObj);
  let data = Object.values(CartObj);

  const handleDelete = (e) => {
    console.log(e);
    if (window.confirm(`Do you want to delete ${CartObj[e].name} ?`)) {
      dispatch(removeFromCart(e));
    }
  };

  const handleRowClick = (e) => {
    console.log("Row Click", CartObj[e]);

    dispatch(addtoStage(CartObj[e]));
  };

  function TipComponet({ id }) {
    return <div>Hi This {id}</div>;
  }
  const handleMouseOver = (e) => {};
  return (
    <DenseTable
      maxHeight={maxHeight}
      headers={headers}
      data={data}
      rowClickHandler={handleRowClick}
      deleteHandler={handleDelete}
      mouseOverHandler={handleMouseOver}
      moreInfo={true}
      MoreInfoComponent={TipComponet}
    />
  );
}
