import { Add } from "@mui/icons-material";
import {
  Autocomplete,
  Button,
  IconButton,
  MenuItem,
  Modal,
  Paper,
  Select,
  TextField,
} from "@mui/material";
import { Box } from "@mui/system";
import QueryString from "qs";
import React, { useEffect, useRef } from "react";
import { useAlert } from "react-alert";
import api from "../../utils/api_handler";
import evalJsonata from "../../utils/evanJsonata";

const style = {
  position: "absolute",
  background: "beige",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 200,
  p: 4,
};
export default function ServerSelect({
  name,
  value,
  handleChange,
  path,
  hideAdd,
}) {
  let alert = useAlert();
  let [items, setItems] = React.useState([]);
  let [timer, setTimer] = React.useState(null);
  let [text, setText] = React.useState("");
  let [newText, setNewText] = React.useState("");
  let [modal, setModal] = React.useState(false);

  let search = async (e, newValue) => {
    clearTimeout(timer);
    setText(newValue);

    setTimer(
      setTimeout(async (_) => {
        let query = QueryString.stringify({
          filters: newValue
            ? {
                name: {
                  $containsi: newValue,
                },
              }
            : {},
        });

        let { status, data, msg } = await api(
          `${path}?${query}`,
          "get",
          null,
          true
        );
        if (status) {
          let list = evalJsonata(
            data,
            `
                  data.$.{
                      "id":id,
                      "name":attributes.name
                  }
                  `
          );

          if (
            typeof list === "object" &&
            !Array.isArray(list) &&
            list !== null
          ) {
            list = [list];
          }
          console.log("list -" + name, list);
          setItems(list);
        }
      }, 500)
    );
  };

  React.useEffect((_) => {
    search(null, null);
  }, []);

  async function createNew() {
    console.log(newText);

    let { status, data, msg } = await api(
      `${path}`,
      "post",
      { data: { name: newText } },
      true
    );

    if (status) {
      let newCreated = evalJsonata(
        data,
        `
              data.$.{
                  "id":id,
                  "name":attributes.name
              }
              `
      );
      setItems([newCreated]);
      console.log("newCreated", newCreated);
      handleChange({ target: { name: name, value: newCreated } });

      alert.success("New Created");
      setNewText(newCreated.name);
      setModal(false);
    } else {
      alert.error(msg);
    }
  }
  return (
    <Box sx={{ display: "flex" }}>
      <Autocomplete
        sx={{ flexGrow: 1 }}
        options={items ?? []}
        value={value[name] == undefined ? "" : value[name] ?? ""}
        onChange={(ev, newValue) =>
          handleChange({ target: { name: name, value: newValue } })
        }
        name={name}
        getOptionLabel={(option) => (option ? option.name : "")}
        id="disable-close-on-select"
        inputValue={text}
        onInputChange={search}
        renderInput={(params) => (
          <TextField
            {...params}
            fullWidth
            error={false}
            id="standard-error-helper-text"
            label={name}
            name={name}
            helperText=""
            variant="standard"
          />
        )}
      />

      {!hideAdd && (
        <IconButton onClick={(_) => setModal(true)} size="small">
          <Add fontSize="small" />
        </IconButton>
      )}

      <Modal open={modal} onClose={(_) => setModal(false)}>
        <Paper sx={style} elevation={8}>
          <TextField
            name="name"
            label={`Add New ${name}`}
            value={newText}
            variant="standard"
            onChange={(_) => setNewText(_.target.value)}
            fullWidth
          />
          <Button onClick={createNew}>Save</Button>
        </Paper>
      </Modal>
    </Box>
  );
}
