import styled from "@emotion/styled";
import * as React from "react";
import { Routes, Route, Redirect, Navigate } from "react-router-dom";
import Billing from "./Billing";
import Barcode from "./Barcode";
import Products from "./products";
import Purchase from "./purchaseOrders";
import Login from "./login";
import InvScreen from "./InventoryEntry";
import BillScreen from "./bills";
import ItemsScreen from "./inventory";
import SupplierScreen from "./supplier";
import ReturnScreen from "./return";
import ReturnListScreen from "./returnList";
import PaymentScreen from "./payments";
import ProtectedRoute from "../utils/protectedRoute";

const Container = styled.div((props) => ({
  height: "calc(100vh - 48px)",
  width: "100vw",
  padding: "16px 24px",
}));

export function Screens() {
  return (
    <Routes>
      <Route path="/products" element={<Products />} />

      <Route path="/bill/:id" element={<Billing />} />
      <Route path="/barcode" element={<Barcode />} />
      <Route path="/bills" element={<BillScreen />} />

      <Route path="/login" element={<Login />} />

      <Route path="/purchase" element={<Purchase />} />
      <Route path="/inventory-entry/:id" element={<InvScreen />} />

      <Route path="/return/:id" element={<ReturnScreen />} />
      <Route path="/returns" element={<ReturnListScreen />} />

      <Route path="/inventory" element={<ItemsScreen />} />
      <Route path="/suppliers" element={<SupplierScreen />} />
      <Route path="/payments" element={<PaymentScreen />} />

      <Route path="/:any" element={<Navigate to="/login" />} />

      <Route path="*" element={<Login />} />
    </Routes>
  );
}
